export default {
	data() {
		return {
			key: '1'
		};
	},
	props: {
		//载入的标签数据
		data: Array

	},
	methods: {
		clickKeyword(e, title,list) {
			// console.log(title)
			this.key = e;
			var data={
				key:e,
				name:title,
				list:list
			}
			this.$emit('clickKeyword', data);
		}
	}
}
